import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.css']
})
export class GameComponent implements OnInit {
  gameBoard = [];
  openCards = [];
  timerInterval;
  gameIsStarted = false;
  gameIsFinished = false;
  m = 0;
  s = 0;
  ms = 0;
  timerDisplay = { m: '00', s: '00', ms: '00' };

  constructor() {
    this.resetTimer = this.resetTimer.bind(this);
    this.timer = this.timer.bind(this);
    this.gameOver = this.gameOver.bind(this);
  }

  ngOnInit() {
    this.loadBoard();
  }

  loadBoard() {
    // var images = [
    //   '../../assets/1.png',
    //   '../../assets/2.jpg',
    //   '../../assets/3.jpg',
    //   '../../assets/4.png',
    //   '../../assets/5.png',
    //   '../../assets/6.png',
    //   '../../assets/7.png',
    //   '../../assets/8.png'
    // ];
    var images = [
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png',
      '../../assets/1.png'
    ];

    images = images.concat(images);

    this.gameBoard = [
      [
        {}, {}, {}, {}
      ],
      [
        {}, {}, {}, {}
      ], [
        {}, {}, {}, {}
      ], [
        {}, {}, {}, {}
      ]
    ];

    for (let i = 0; i < this.gameBoard.length; i++) {
      var row = this.gameBoard[i];
      for (let j = 0; j < row.length; j++) {
        var randIndex = Math.floor(Math.random() * images.length);
        row[j].cardImage = images.splice(randIndex, 1)[0];
        row[j].isOpen = false;
        row[j].isHidden = false;
      }
    }
  }

  openCard(cell) {
    if (cell.isOpen == true || this.openCards.length >= 2) {
      return;
    } else if (cell.isOpen == false && this.openCards.length == 0) {
      this.openCards.push(cell);
      cell.isOpen = true;
    } else if (cell.isOpen == false && this.openCards.length == 1) {

      this.openCards.push(cell);
      cell.isOpen = true;
      setTimeout(() => {
        for (let i = 0; i < this.openCards.length; i++) {
          const element = this.openCards[i];
          element.isOpen = false;
          if (this.openCards[0].cardImage == cell.cardImage) {
            element.isHidden = true;
          }
        }
        this.openCards = [];
        this.gameOver();
      }, 1000);
    }
  }




  resetTimer() {
    this.m = 0;
    this.s = 0;
    this.ms = 0;

    if (this.ms < 10) {
      this.timerDisplay.ms = "0" + this.ms;
    }
    else {
      this.timerDisplay.ms = this.ms.toString();
    }
    if (this.s < 10) {
      this.timerDisplay.s = "0" + this.s;
    }
    else {
      this.timerDisplay.s = this.s.toString();
    }
    if (this.m < 10) {
      this.timerDisplay.m = "0" + this.m;
    }
    else {
      this.timerDisplay.m = this.m.toString();
    }
  }

  timer() {

    this.ms++;

    if (this.ms > 9) {
      this.ms = 0;
      this.s++;
    }

    if (this.s > 59) {
      this.s = 0;
      this.m++;
    }

    if (this.ms < 10) {
      this.timerDisplay.ms = "0" + this.ms;
    }
    else {
      this.timerDisplay.ms = this.ms.toString();
    }
    if (this.s < 10) {
      this.timerDisplay.s = "0" + this.s;
    }
    else {
      this.timerDisplay.s = this.s.toString();
    }
    if (this.m < 10) {
      this.timerDisplay.m = "0" + this.m;
    }
    else {
      this.timerDisplay.m = this.m.toString();
    }
  }

  startGame() {
    this.gameIsStarted = true;
    this.resetTimer();
    this.timerInterval = setInterval(this.timer, 100);
  }

  gameOver() {
    var gameIsOver = true;
    for (let i = 0; i < this.gameBoard.length; i++) {
      var row = this.gameBoard[i];
      for (let j = 0; j < row.length; j++) {
        if (row[j].isHidden == false) {
          gameIsOver = false;
        }
      }
    }
    if (gameIsOver == true) {
      clearInterval(this.timerInterval);
      setTimeout(() => {
        this.loadBoard();
        this.gameIsFinished = true;
        this.gameIsStarted = false;
      }, 700);
    }
  }
}
